<!--交易记录-->
<template>
  <div class="TransactionRecord" ref="Transaction">
    <el-card class="box1" v-if="type">
      <!-- 标题 -->
      <div class="nav_bar">
        <div style="font-weight: 700;">{{msg.name}}</div>
        <div class="box_1" style="color: rgb(66, 189, 243);cursor: pointer;">注释
          <div class="box_2">
            <div>{{msg.notes}}</div>
          </div>
        </div>
      </div>
      <!-- 时间选择器、查询 -->
      <div style="display: flex;margin-top: 15px;">
        <el-date-picker class="dataInput" v-model="value1" type="datetimerange" :picker-options="pickerOptions"
          start-placeholder="开始日期" end-placeholder="结束日期" align="right" :default-time="['00:00:00', '23:59:59']"
          value-format="yyyy-MM-dd HH:mm:ss">
        </el-date-picker>
        <div>
          <el-button @click='query_name(1)' style="height: 25px; line-height: 10px;" type="primary" size="mini">查询
          </el-button>
        </div>
      </div>
      <!-- 下级行网点/下拉框 -->
      <div v-if="!msg.type" class="nav_title">
        <div @click="typeClick(1)" :class="{'colorSize':typeShow,'size':true}">看下级行</div>
        <div @click="typeClick(2)" :class="{'colorSize':!typeShow,'size':true}">看网点</div>
      </div>
      <div v-else style="display: flex;margin-top: 15px;font-size: 12px;">
        <el-select size="mini" v-model="value" placeholder="请选择" @change="changeClick">
          <el-option v-for="item in options" :key="item.company_id" :label="item.company_name" :value="item.company_id">
          </el-option>
        </el-select>
      </div>
      <!-- 数据总计、分页 -->
      <div class="title">
        <div style="margin-right: 30px;line-height: 32px;display: flex;">总计：
          <div style="min-width:25px;margin-right: 5px;text-align: center;">
            <span class="colorSize">{{count_num}}</span>
          </div>{{msg.Company}}
        </div>
        <el-pagination small @current-change="handleCurrentChange" background layout="prev, pager, next"
          :total="totalRow">
        </el-pagination>
      </div>
      <div style="width: 100%;margin-top: 15px;">
        <div style="width: 100%;margin-top: 8px;" v-for="(item, index) in tableData" :key="index">
          <div style="width: 100%;display: flex;justify-content: space-between;font-size: 12px;">
            <div> {{queryParam.currentPage>1?(queryParam.currentPage-1)*10+index+1:index+1}} &#x3000;
              <span v-if="msg.type">{{item.bmi_mer_name}}(</span>{{item.company_name}}<span v-if="msg.type">)</span>
            </div>
            <div>{{msg.Company=='元'?item.count_num.toFixed(2):item.count_num}}</div>
          </div>
          <div style="width: 100%;height: 10px;background-color: rgb(215, 215, 215);">
            <div
              :style="{'width':(item.count_num=='0'?0:item.count_num/count*100)+'%' ,'background-color': '#00bfbf','height': '10px'}">
            </div>
          </div>
        </div>
      </div>
    </el-card>
    <el-card class="box2" v-else>
      <!-- 标题 -->
      <div class="nav_bar">
        <div style="font-weight: 700;">{{msg.name}}</div>
        <div class="box_1" style="color: rgb(66, 189, 243);cursor: pointer;">注释
          <div class="box_2">
            <div>{{msg.notes}}</div>
          </div>
        </div>
      </div>
      <!-- 时间选择器、查询 -->
      <div style="display: flex;margin-top: 15px;">
        <el-date-picker class="dataInput" v-model="value1" type="datetimerange" :picker-options="pickerOptions"
          start-placeholder="开始日期" end-placeholder="结束日期" align="right" :default-time="['00:00:00', '23:59:59']"
          value-format="yyyy-MM-dd HH:mm:ss">
        </el-date-picker>
        <div>
          <el-button @click='query_name(1)' style="height: 25px; line-height: 10px;" type="primary" size="mini">查询
          </el-button>
        </div>
      </div>
      <!-- 下级行网点/下拉框 -->
      <div v-if="!msg.type" class="nav_title">
        <div @click="typeClick(1)" :class="{'colorSize':typeShow,'size':true}">看下级行</div>
        <div @click="typeClick(2)" :class="{'colorSize':!typeShow,'size':true}">看网点</div>
      </div>
      <div v-else style="display: flex;margin-top: 15px;font-size: 12px;">
        <el-select size="mini" v-model="value" placeholder="请选择" @change="changeClick">
          <el-option v-for="item in options" :key="item.company_id" :label="item.company_name" :value="item.company_id">
          </el-option>
        </el-select>
      </div>
      <!-- 数据总计、分页 -->
      <div class="title">
        <div style="margin-right: 30px;line-height: 32px;display: flex;">总计：
          <div style="min-width:25px;margin-right: 5px;text-align: center;">
            <span class="colorSize">{{count_num}}</span>
          </div>{{msg.Company}}
        </div>
        <div style="text-align: left;">
          <el-pagination small @current-change="handleCurrentChange" background layout="prev, pager, next"
            :total="totalRow">
          </el-pagination>
        </div>
      </div>
      <div style="width: 100%;margin-top: 15px;">
        <div style="width: 100%;margin-top: 8px;" v-for="(item, index) in tableData" :key="index">
          <div style="width: 100%;display: flex;justify-content: space-between;font-size: 12px;">
            <div> {{queryParam.currentPage>1?(queryParam.currentPage-1)*10+index+1:index+1}} &#x3000;
              <span v-if="msg.type">{{item.bmi_mer_name}}(</span>{{item.company_name}}<span v-if="msg.type">)</span>
            </div>
            <div>{{msg.Company=='元'?item.count_num.toFixed(2):item.count_num}}</div>
          </div>
          <div style="width: 100%;height: 10px;background-color: rgb(215, 215, 215);">
            <div
              :style="{'width':(item.count_num=='0'?0:item.count_num/count*100)+'%' ,'background-color': '#00bfbf','height': '10px'}">
            </div>
          </div>
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
  export default {
    name: "TransactionRecord",
    data() {
      return {
        scale: 'scale(1)',
        options: [],
        value: '*',
        count_num: 0,
        count: 0,
        typeShow: true,
        typeValue: 1,
        totalRow: 0,
        /** 查询参数 */
        queryParam: {
          totalRow: 0,
          currentPage: 1,
          pageSize: this.customGlobal.pageSize,
        },
        pickerOptions: {
          shortcuts: [
            {
              text: '近三十天',
              onClick(picker) {
                const end = new Date().toLocaleString();
                var start = new Date(new Date().getTime() - 3600 * 1000 * 24 * 30).toLocaleString();
                start = start.substr(0, 11) + '00:00:00'
                console.log('res==', start.substr(11, 8));
                picker.$emit('pick', [start, end]);
              }
            }, {
              text: '当月',
              onClick(picker) {
                const end = new Date().toLocaleString();
                const start = new Date(new Date().getFullYear(), new Date().getMonth(), 1).toLocaleString();
                console.log([start, end]);
                picker.$emit('pick', [start, end]);
              }
            }]
        },
        value1: [],
        value2: '',
        tableData: []
      }
    },
    created() {

      Date.prototype.toLocaleString = function () {
        return this.getFullYear() + "-" + (this.getMonth() > 9 ? this.getMonth() + 1 : '0' + (this.getMonth() + 1)) + "-" + (this.getDate() > 9 ? this.getDate() : '0' + this.getDate()) + " " + (this.getHours() > 9 ? this.getHours() : '0' + this.getHours()) + ":" + (this.getMinutes() > 9 ? this.getMinutes() : '0' + this.getMinutes()) + ":" + (this.getSeconds() > 9 ? this.getSeconds() : '0' + this.getSeconds());
      };
      const end = new Date().toLocaleString();
      var start = new Date(new Date().getTime() - 3600 * 1000 * 24 * 30).toLocaleString();
      start = start.substr(0, 11) + '00:00:00'
      this.value1 = [start, end]
      this.queryParam.currentPage = 1;
      this.query_name();
      this.queryList();


    },
    mounted() {
      let ref = this.$refs.Transaction
      this.scale = 'scale('+ ref.offsetWidth / 600 +')'
    },
    props: ['msg', 'type', 'list'],
    watch: {
      'list.id'(newVal) {
        const end = new Date().toLocaleString();
        var start = new Date(new Date().getTime() - 3600 * 1000 * 24 * 30).toLocaleString();
        start = start.substr(0, 11) + '00:00:00'
        this.value1 = [start, end]
        console.log(this.customGlobal.userInfoExt.cui_type, '===');
        this.queryParam.currentPage = 1;
        this.query_name();
        this.queryList();
      }
    },
    methods: {
      changeClick() {
        this.queryParam.currentPage = 1
        this.query_name()
      },
      typeClick(val) {
        this.queryParam.currentPage = 1
        switch (val) {
          case 1:
            this.typeShow = true
            break;
          case 2:
            this.typeShow = false
            break;

          default:
            break;
        }
        this.query_name()
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
        this.queryParam.currentPage = val;
        this.query_name();
      },
      //查询可选下级行
      queryList(val) {
        if (!this.msg.type) {
          return
        }
        let that = this;
        let id
        let cui_type
        if (this.type) {
          cui_type = this.list.type
        } else {
          cui_type = this.customGlobal.userInfoExt.cui_type
        }
        switch (cui_type) {
          case '0':
            id = this.list.id
            break;

          case '1':
            if (this.type) {
              id = this.list.id
            } else {
              id = this.customGlobal.userInfoExt.cui_bank_center_id
            }
            break;

          case '2':
            if (this.type) {
              id = this.list.id
            } else {
              id = this.customGlobal.userInfoExt.cui_province_id
            }
            break;

          case '3':
            if (this.type) {
              id = this.list.id
            } else {
              id = this.customGlobal.userInfoExt.cui_city_id
            }
            break;
          case '4':
            if (this.type) {
              id = this.list.id
            } else {
              id = this.customGlobal.userInfoExt.cui_county_id
            }
            break;
          case '5':
            if (this.type) {
              id = this.list.id
            } else {
              id = this.customGlobal.userInfoExt.cui_station_id
            }
            break;

          default:
            break;
        }
        that.options = [{ company_id: '*', company_name: '全部' }];
        console.log('--------------------------------------------------------------------------');
        let _jsondata_ = {
          cui_type: cui_type,
          company_id: id,
        };
        const loading = this.$loading({
          lock: true,
          text: '提交中...',
          target: '.main',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0)'
        });
        var a1 = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.postQueryChildernCompanyList,
          //this.customGlobal.serverUrl.baseURI,
          this.customGlobal.serverUrl.orgBaseURI,
          { "Content-Type": "application/x-www-form-urlencoded" },
          this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
        );
        a1.then((res) => {
          if (res.data.status === 'success') {
            this.options = this.options.concat(res.data.rows)
          } else {
            console.info("查询失败");
            loading.close();
            if (res.data.msg === '请登录认证') {
              this.$router.push({ path: '/login' });
            }
          }
        }, error => {
          loading.close();
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(error.message), '查询失败!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:" + JSON.stringify(res));
          loading.close();
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(res.message), '查询失败', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });
      },

      //查询
      /**
      *  @param //bauti_bank_center_id
      *  @param //bauti_province_id
      *  @param //bauti_city_id
      *  @param //bauti_county_id
      *  @param //bai_cui_type
      *  @param //start_time
      *  @param //end_time
      *  @param //perPageSize  bai_cui_type
      *  @param //currentPage
      *  @param //level 1,是看下级行，2是看网点
      *  @return //rows1.count_num 总计
      **/
      query_name(val) {
        console.log('res==', this.list);
        if (!this.value1) {
          this.$message({
            message: '日期不可为空!',
            type: 'warning'
          });
          return
        }
        switch (val) {
          case 1:
            this.queryParam.currentPage = 1
            break;
          default:
            break;
        }
        console.log(this.msg, '---------------');
        let that = this;
        that.tableData = [];
        let cui_type
        let cui_bank_center_id = ''
        let cui_province_id = ''
        let cui_city_id = ''
        let cui_county_id = ''
        let cui_station_id = ''
        let _jsondata_
        if (this.type) {
          cui_type = this.list.type
        } else {
          cui_type = this.customGlobal.userInfoExt.cui_type
        }
        let id
        switch (cui_type) {
          case '0':
            id = this.list.id
            break;
          case '1':
            if (this.type) {
              id = this.list.id
              cui_bank_center_id = this.list.id
            } else {
              id = this.customGlobal.userInfoExt.cui_bank_center_id
              cui_bank_center_id = this.customGlobal.userInfoExt.cui_bank_center_id
            }
            break;

          case '2':
            if (this.type) {
              id = this.list.id
              cui_province_id = this.list.id
            } else {
              id = this.customGlobal.userInfoExt.cui_province_id
              cui_province_id = this.customGlobal.userInfoExt.cui_province_id
            }
            break;

          case '3':
            if (this.type) {
              id = this.list.id
              cui_city_id = this.list.id
            } else {
              id = this.customGlobal.userInfoExt.cui_city_id
              cui_city_id = this.customGlobal.userInfoExt.cui_city_id
            }
            break;
          case '4':
            if (this.type) {
              id = this.list.id
              cui_county_id = this.list.id
            } else {
              id = this.customGlobal.userInfoExt.cui_county_id
              cui_county_id = this.customGlobal.userInfoExt.cui_county_id
            }
            break;
          case '5':
            if (this.type) {
              id = this.list.id
              cui_station_id = this.list.id
            } else {
              id = this.customGlobal.userInfoExt.cui_station_id
              cui_station_id = this.customGlobal.userInfoExt.cui_station_id
            }
            break;

          default:
            break;
        }
        if (this.msg.type) {
          _jsondata_ = {
            start_time: this.value1[0],
            end_time: this.value1[1],
            level: this.typeShow ? '1' : '2',//是看下级行，2是看网点
            bai_cui_type: cui_type,
            perPageSize: that.queryParam.pageSize,
            currentPage: that.queryParam.currentPage,
            [this.msg.query_company_id]: that.value,
            [this.msg.cur_company_id]: id ? id : '*',
          };
        } else {
          _jsondata_ = {
            start_time: this.value1[0],
            end_time: this.value1[1],
            level: this.typeShow ? '1' : '2',//是看下级行，2是看网点
            bai_cui_type: cui_type,
            perPageSize: that.queryParam.pageSize,
            currentPage: that.queryParam.currentPage,
            [this.msg.bank_center_id]: cui_bank_center_id,
            [this.msg.province_id]: cui_province_id,
            [this.msg.city_id]: cui_city_id,
            [this.msg.county_id]: cui_county_id,
            flag: this.msg.flag ? this.msg.flag : ''
          };
        }

        const loading = this.$loading({
          lock: true,
          text: '提交中...',
          target: '.main',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0)'
        });
        var a1 = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl[this.msg.api],
          //this.customGlobal.serverUrl.baseURI,
          this.customGlobal.serverUrl.orgBaseURI,
          { "Content-Type": "application/x-www-form-urlencoded" },
          this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
        );
        a1.then((res) => {
          if (res.data.status === 'success') {
            loading.close();
            this.count_num = res.data.rows1[0].count_num
            that.tableData = res.data.rows;
            that.totalRow = res.data.total;
            if (that.queryParam.currentPage == 1 && that.tableData.length > 0) {
              this.count = that.tableData[0].count_num
            }
          } else {
            console.info("查询失败");
            loading.close();
            if (res.data.msg === '请登录认证') {
              this.$router.push({ path: '/login' });
            }
          }
        }, error => {
          loading.close();
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(error.message), '查询失败!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:" + JSON.stringify(res));
          loading.close();
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(res.message), '查询失败', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });
      },

    }
  }
</script>

<style scoped>
  .box2 {
    width: 100%;
    height: 600px;
    padding: 20px 10px;
    box-sizing: border-box;
  }

  .box1 {
    width: 100%;
    height: 570px;
    padding: 20px 10px;
    box-sizing: border-box;
  }

  .dataInput {
    width: 300px !important;
    height: 25px;
    margin-right: 30px;
  }

  .dataInput>>>.el-icon-time:before {
    content: "";
  }

  .dataInput>>>.el-range-editor.el-input__inner {
    padding: 0;
  }

  .dataInput>>>.el-date-editor .el-range-input {
    background-color: transparent;
    color: white;
  }

  .dataInput>>>.el-range-separator {
    line-height: 15px !important;
  }

  .dataInput>>>.el-range-input {
    font-size: 12px !important;
    width: 115px;
  }


  .dataInput /deep/ .el-input__icon {
    line-height: 19px !important;
  }

  .colorSize {
    border-bottom: rgb(66, 189, 243) 1px solid;
    color: rgb(66, 189, 243);
  }

  .box_2:hover .box_2 {
    display: block;
  }

  .box_1:hover .box_2 {
    display: block !important;
  }

  .box_2 {
    display: none;
    width: 200px;
    position: absolute;
    background: #FFFFFF;
    color: #000000;
    top: 25px;
    right: 9px;
    text-align: left;
    border: #000000 solid 1px;
    z-index: 1;

  }

  .nav_bar {
    display: flex;
    justify-content: space-between;
    color: #027db4;
    font-size: 14px;
    position: relative;
  }

  .title {
    display: flex;
    margin-top: 15px;
    font-size: 12px;
    justify-content: space-between;
    text-align: left;
  }

  .size {
    text-align: left;
    cursor: pointer;
  }

  .nav_title {
    display: flex;
    margin-top: 15px;
    font-size: 12px;
    width: 110px;
    justify-content: space-between;
  }
</style>


